import Base from 'modules/base'
import * as xhr from 'modules/fetch'
import bind from 'delegate'
import { Manager, MODAL_IS_SHOW, MODAL_IS_OPEN, ACCOUNT_ADDRESS_LEAVE_MODAL, CHECK_RESPONSE } from 'lib/events'
import { select, getCollection } from 'lib/selectors'
import { focusElement, getSelectedOptionValue } from 'lib/helpers'
import popin from 'modules/popin'
import {AddAnAddressManager} from "./add-an-address";
import * as MobilePhoneValidation from '../../config/mobile-phone-validation'


// Selectors
const MODIFY_ADDRESS_CONTAINER_SELECTOR = 'div[id=modifyAddressContainer]'
const MODIFY_ADDRESS_CANCEL_BUTTON_SELECTOR = 'button[type=button].js-add-edit-address-cancel'
const MODIFY_ADDRESS_FORM_SELECTOR = 'form[id=addressForm]'
const MODIFY_ADDRESS_DIALOG_SELECTOR = 'div[id="js-modal-prompt-leave-modify-address-form"]'
const DIALOG_LEAVE_BUTTON_SELECTOR = 'button[id="modal_leave_buttons_ok"]'
const DIALOG_CANCEL_BUTTON_SELECTOR = 'button[id="modal_leave_buttons_ko"]'
const DIALOG_CLOSE_BUTTON_SELECTOR = 'div.yes-no-modal button.popin__close.js-popin-close'
const DIALOG_WRONG_ADDRESS_VALID_BUTTON_SELECTOR = 'button.is-tertiary.js-popin-close'
const DIALOG_WRONG_ADDRESS_CROSS_BUTTON_SELECTOR = 'button.popin__close.js-popin-close'
const SET_DEFAULT_CHECKBOX = 'input#makeDefault'
const COUNTRY_SELECT = '.is-myaccount-select > select[name="countryIso"]'

class EditAnAddressManager extends Base {

  constructor(el) {
    if (config.country.toUpperCase() === 'CN') {
        import ('./cn-address').then((module) => {
            module.default.init()
        })
    }
    super(el)
    this.el = el
    this.params = {
      addressId: null,
      clickedElm: null,
      cancelLeaveModalIsDisplayed: false,
      eCommercecountry: true
    }
    this.bindEvents()
    this.getCurrentCountryType() 
  }

  /**
   * Called to set events
   */
  bindEvents() {
    bind(this.el, MODIFY_ADDRESS_FORM_SELECTOR, 'submit', this.onSaveAddressButtonClicked)
    bind(this.el, MODIFY_ADDRESS_CANCEL_BUTTON_SELECTOR, 'click', this.onCancelButtonClicked)
    bind(document.body, DIALOG_LEAVE_BUTTON_SELECTOR, 'click', this.onDialogLeaveButtonClicked)
    bind(document.body, DIALOG_CANCEL_BUTTON_SELECTOR, 'click', this.onDialogCancelButtonClicked)
    bind(document.body, DIALOG_CLOSE_BUTTON_SELECTOR, 'click', this.onDialogCancelButtonClicked)
    bind(document.body, DIALOG_WRONG_ADDRESS_VALID_BUTTON_SELECTOR, 'click', this.onUserConfirmedWrongAddress)
    bind(document.body, DIALOG_WRONG_ADDRESS_CROSS_BUTTON_SELECTOR, 'click', this.onDialogCancelButtonClicked)
    bind(document.body, SET_DEFAULT_CHECKBOX, 'click', this.onEnableRadioButtons)
    bind(document.body, COUNTRY_SELECT, 'change', this.onCountrySelectorUpdated)
    Manager.subscribe(MODAL_IS_SHOW, this.onModalShow)
    Manager.subscribe(MODAL_IS_OPEN, this.onModalOpened)
    document.addEventListener('mousedown', this.onDocumentClicked)
  }

  /**
   * Save an address by sending a form to server
   * @param form {HTMLFormElement}: contains address values
   */
  saveAddress(form, userConfirmedAddress = false) {
    // Add extra parameter to validate or not a wrong address
    const skipValidationElm = document.createElement('input')
    skipValidationElm.innerHTML = `<input id="skipValidation" name="skipValidation" type="hidden" value="${userConfirmedAddress}">`
    form.appendChild(skipValidationElm.firstChild)

    this.manageDataObj(form)
    .then(paramsData => {
      xhr.submit(form, { data: paramsData })
      .then((data) => {
        this.returnToAddressesBook()
        return data
      })
      .catch(data => {
        if ('modal' in data) {
          this.params.tmpAddressesList = data.html
          this.displayWrongAddressDialog(data)
        } else if (data.fields) {
          const payload = {
            'data': data,
            'form': form
          }
          Manager.publish(CHECK_RESPONSE, payload)
        }
      })
    })
  }

/**
 * Return new object from Form data
 */
  manageDataObj(form) {
    return new Promise((resolve) => {
      const paramsData = {}
      const inputs = getCollection('input, select', form)
      let isoCode
      const namesToExclude = ['phoneCountryCode', 'addressId', 'countryIso', 'useDeliveryAddressEU', '_useDeliveryAddressEU', 'qualityCode', 'skipValidation', '_shippingToBusinessAddress', 'billingAddress', 'shippingAddress', 'line2', 'district', 'provincia', 'shippingToBusinessAddress', 'defaultAddress', 'municipality','village', 'modifyDefaultBilling', 'building']
      if((config.country.toLowerCase() === "ru") || (config.country.toLowerCase() === "cn")){
        namesToExclude.push("postcode")      
      }
      Array.from(inputs).filter(input =>
        // We add an exception for phoneCountryCode && addressId as this field is used to normalized the
        // required phoneCountryCodeField input && addressId
         (input.required || namesToExclude.indexOf(input.name) !== -1)).forEach(input => {
           if (input.type === 'radio' || input.type === 'checkbox') {
             input.checked === true ? paramsData[input.name] = input.value : null
           } else if (input.name === 'countryIso') {
             isoCode = input.value === 'LT' ? input.value : null
             paramsData[input.name] = input.value
           } else if((config.country.toLowerCase() === "cn") && ((input.name === "provincia") || (input.name === "district") || (input.name === "townCity"))) {
              paramsData[input.name] = input.selectedOptions[0].innerText;
           } else if (input.name === 'postcode') {
             input.value.indexOf('LT') !== -1 ? input.value = input.value.replace(/[^0-9]/g, '') : null
             paramsData[input.name] = isoCode ? `${isoCode}-${input.value}` : input.value
           }
           else {
             input.value !== '' ? paramsData[input.name] = input.value : null
           }
         })
      resolve(paramsData)
    })
  }

  /**
   * Return user to addresses book page
   */
  returnToAddressesBook() {
    const url = `${document.location.origin}`+ window.config.pages.account.addressBook;
    window.location.replace(url)
  }

  /**
   * Check is passed element is descending of add address form
   * @param elm {EventTarget}: element to check
   * @return {boolean}: true is passed element is inside of form, else false
   */
  isDescending(elm) {
    const addAddressContainerElement = select(MODIFY_ADDRESS_CONTAINER_SELECTOR, this.el)
    return addAddressContainerElement.contains(elm)
  }

  /**
   * display a yes(leave) no(cancel) dialog
   */
  displayLeaveCancelDialog() {
    this.params.cancelLeaveModalIsDisplayed = true
    const dialogElm = select(MODIFY_ADDRESS_DIALOG_SELECTOR, this.el)
    popin.open({
      html: dialogElm.innerHTML,
      role: 'dialog',
      modifiers: '',
      opener: null,
      manageFocus: true,
      forceInert: true,
      modal: false,
      action: 'dialogLeaveCancel',
      outerClose: false
    })
    // Catch keyboard inputs (enter, a, b, esp ...)
    window.addEventListener('keydown', this.onEscapeButtonClicked)
    document.removeEventListener('mousedown', this.onDocumentClicked)
  }

  /**
   * Close the active modal
   */
  closeModal() {
    this.params.cancelLeaveModalIsDisplayed = false
    popin.hide()
    document.addEventListener('mousedown', this.onDocumentClicked)
    this.notifyAccountAddressLeaveModal(false)
  }

  /**
   * Call this to display a "wrong address" dialog
   * @param data {Object}: JSON response
   */
  displayWrongAddressDialog(data) {
    popin.open({
      html: data.modal,
      role: 'dialog',
      modifiers: '',
      opener: null,
      manageFocus: false,
      forceInert: true,
      modal: true,
      page: 'wrong address',
      outerClose: false
    })
    window.addEventListener('keydown', this.onEscapeButtonClicked)
    document.removeEventListener('mousedown', this.onDocumentClicked)
  }


  // Events

  /**
   * Triggered once user click on save address button
   * @param e {MouseEvent}: contains event parameters
   */
  onSaveAddressButtonClicked = (e) => {
    const form = e.delegateTarget
    if (form.invalid.length) {
      return
    }

    e.preventDefault()
    const input = select('[data-phone-pattern]');
    if (input) {
      MobilePhoneValidation.getUserNumber()
      .then((isValid) => {
        this.saveAddress(form, false)
      })
    }
    else {
      this.saveAddress(form, false)
    }
    
  }

  /**
   * Triggered once user click on cancel address button
   * @param e {MouseEvent}: contains event parameters
   */
  onCancelButtonClicked = (e) => {
    this.displayLeaveCancelDialog()
  }

  /**
   * Used to detect if target element is inside form element
   * @param e {MouseEvent}: mouse event properties
   */
  onDocumentClicked = (e) => {

    if (!this.params.cancelLeaveModalIsDisplayed) {

      const clickedElement = e.target
      const parentElementName = clickedElement.parentNode.tagName.toLowerCase()
      const clickedElementName = clickedElement.tagName.toLowerCase()
      const buttons = ['a', 'button']
      const elements = [parentElementName, clickedElementName]

      // Is a BUTTON or A element
      if (elements.indexOf(buttons[0]) > -1 || elements.indexOf(buttons[1]) > -1) {
        this.params.clickedElement = clickedElement

        // Is not descending element and modal is not displayed
        if (!this.isDescending(clickedElement)) {
          e.preventDefault()
          this.notifyAccountAddressLeaveModal(true)
          this.displayLeaveCancelDialog()
        }
      }
    }
  }

  /**
   * Triggered once user clicked on keyboard inputs
   * @param e {MouseEvent}: MouseEvent parameters
   */
  onEscapeButtonClicked = (e) => {
    if ((e.key === 'Escape'|| e.key === 'Esc'|| e.keyCode === 27)) {
      this.onDialogCancelButtonClicked(e)
    }
  }


  /**
   * Removes attribute CHECKED on type=select && type=radio, on country changement
   */
  onCountrySelectorUpdated = () => {
    this.onDisableInputs()
    this.getCurrentCountryType()
  }

  /**
   * Cleans radio buttons and text
   */
  onDisableInputs = () => {
    const radioContainer = select('.add-edit-account-addresses', document.body)
    const radioButtons = getCollection('.add-edit-account-addresses input[type=radio]', document.body)
    const checkbox = select(SET_DEFAULT_CHECKBOX, document.body)
    if (checkbox) {
      checkbox.checked = checkbox.data('checked') || false
      checkbox.data('checked', '')
    }
    if (radioContainer){
      radioContainer.classList.add('is-hidden')
      radioButtons.forEach(radio => {
        radio.checked = false
        radio.setAttribute('disabled', "disabled")
        radio.removeAttribute('required')
        radio.setAttribute('aria-required', false)
      })
    }
    const onlyBillingText = select('.js-account-billing-text', document.body)
    if (onlyBillingText) {
      !onlyBillingText.classList.contains('is-hidden') ? onlyBillingText.classList.add('is-hidden') : null
    } 
  }

  /**
   * Triggered once user clicked on input checkbox
   * 
   */
  onEnableRadioButtons = () => {
    const checkbox = select(SET_DEFAULT_CHECKBOX, document.body)
    const checked = checkbox.checked
    const radioContainer = select('.add-edit-account-addresses',document.body)
    const radioButtons = getCollection('.add-edit-account-addresses input[type=radio]', document.body)
    
    
    if (checked) {
      radioContainer.classList.remove('is-hidden')
      this.params.eCommercecountry === true ? this.onActivateButtons(radioButtons) : this.onChooseBillingButton(radioButtons)
    } else {
      const onlyBillingText = select('.js-account-billing-text', document.body)
      if (onlyBillingText) {
        !onlyBillingText.classList.contains('is-hidden') ? onlyBillingText.classList.add('is-hidden') : null
      } 
      radioContainer.classList.add('is-hidden')
      radioButtons.forEach(radio => {
        radio.checked = false
        radio.setAttribute('disabled', "disabled")
        radio.removeAttribute('required')
        radio.setAttribute('aria-required', false)
      })
    }
  }

  /**
   * Returns true if selected country is ecommerce country
   */
  getEcommerceCountry = (countryISO) => {
    return new Promise((resolve) => {
      const arrCountries = window.config.locales.account.ecommCountries;
      const ecommerceCountry = arrCountries.find(country => country === countryISO)
      let isEcommerce
      isEcommerce = ecommerceCountry ? true : false 
      resolve(isEcommerce)
    })
  }

  /**
   * Returns parameter isEcommerce<Boolean> according to selected country  ecommerce type
   */
  getCurrentCountryType = () => {
    const selectorCountry = select(COUNTRY_SELECT, document.body)
    let countrySelected
    selectorCountry ?  countrySelected = getSelectedOptionValue(selectorCountry,'upper') : countrySelected = null
    if (countrySelected) {
      this.getEcommerceCountry(countrySelected)
      .then(isEcommerce => {
        const inputBillingAddress = select('input[id="billingAddress"]', document.body)
        
        isEcommerce === true ? inputBillingAddress.value = 'false' : inputBillingAddress.value = 'true'

        return this.params.eCommercecountry = isEcommerce 
      })
    }
    else {
      return false
    }
  }

  /**
   * Enables radio buttons and checks the first one
   */
  onActivateButtons = (radioButtons) => {
    radioButtons.forEach(radio => {
      radio.removeAttribute('disabled')
      radio.setAttribute('required', true)
      radio.setAttribute('aria-required', true)
    })
    radioButtons[0].checked = radioButtons[0].data('checked') || true
    radioButtons[0].data('checked', true)
  }

  /**
   * Enables billing radio button and checks it
   */
  onChooseBillingButton = (radioButtons) => {
    const billingRadio = radioButtons.find(radio => radio.id === 'defaultTypeBilling')
    const onlyBillingText = select('.js-account-billing-text', document.body)
    onlyBillingText ? onlyBillingText.classList.remove('is-hidden') : null
    billingRadio.removeAttribute('disabled')
    billingRadio.setAttribute('required', true)
    billingRadio.setAttribute('aria-required', true)
    billingRadio.checked = billingRadio.data('checked') || true
    billingRadio.data('checked', true)
  }

  /**
   * Triggered once user click on "Cancel" from popin
   * Don't use it for other purposes
   * @param e {MouseEvent}: contains event properties
   */
  onDialogCancelButtonClicked = (e) => {
    e.stopImmediatePropagation()
    this.closeModal()
    window.removeEventListener('keydown', this.onEscapeButtonClicked)
  }

  /**
   * Triggered once user click on "Leave" button from popin
   * Don't use it for other purposes
   * @param e {MouseEvent}: contains event properties
   */
  onDialogLeaveButtonClicked = () => {
    this.returnToAddressesBook()
  }

  /**
   * Triggered once user confirmed is "correct address"
   */
  onUserConfirmedWrongAddress = () => {
    const formElm = select(MODIFY_ADDRESS_FORM_SELECTOR, this.el)
    this.saveAddress(formElm, true)
  }



  /**
   * used to applied a focus on the modal cross button
   * @param params {Object}: contains modal parameters
   */
  onModalShow = (params) => {
    // applied focus on close button
    const crossButtonElm = select(DIALOG_WRONG_ADDRESS_CROSS_BUTTON_SELECTOR, params.el)
    focusElement(crossButtonElm)
  }

  /**
   * Triggered once dialog is displayed
   * @param params {Object}: contains dialog params (returned by popin.js)
   */
  onModalOpened = (params) => {
    if (params.action === 'dialogLeaveCancel') {
      // add and set attributes
      params.modal.el.setAttribute('role', 'dialog')
      params.modal.el.setAttribute('aria-labelledby', 'modal-leave-title')
    }
  }

  /**
   * Notify/publish ACCOUNT_ADDRESS_LEAVE_MODAL status (for header)
   * @param isModalOpened {Boolean}: tell if modal is opened or closed
   */
  notifyAccountAddressLeaveModal(isModalOpened) {
    // If we clicked on a header link
    const isLeavingFormFromHeader = this.params.clickedElement.closest('.js-header')

    Manager.publish(ACCOUNT_ADDRESS_LEAVE_MODAL, {
      isLeavingFormFromHeader,
      isModalOpened,
      clickedElement: this.params.clickedElement
    })
  }

}

export default function (el) {
  new EditAnAddressManager(el)
}
